import { Component, Inject, OnInit } from '@angular/core';
import { IDeposito } from '../../models/deposito';
import { DepositosService } from '../../service/depositos.service';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { AppAlertService } from 'app/shared/service/app/alert.service';
import { JedsMenuService } from '@shared/service/app/jeds-menu.service';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
@Component({
    selector: 'app-save-depositos',
    templateUrl: './save.component.html',
    styleUrls: ['./save.component.scss']
})
export class SaveComponent implements OnInit {
    columns = ['nombre', 'activo', 'acciones'];
    form: FormGroup;
    deposito: IDeposito;
    title: string;
    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private depositosService: DepositosService,
        public alert: AppAlertService,
        public dialogRef: MatDialogRef<SaveComponent>
    ) {
        this.deposito = this.data ? this.data : null;
        this.title = this.deposito?.iddeposito ? 'Editar depósito' : 'Nuevo depósito';
    }

    ngOnInit(): void {
        this.newForm();
    }

    newForm() {
        this.form = new FormGroup({
            descripcion: new FormControl((this.deposito ? this.deposito.descripcion : ''), Validators.required),
            activo: new FormControl((this.deposito ? (this.deposito?.activo ? 1 : 0) : ''), Validators.required),
        });
    }

    onSubmit() {
        if (!this.form.valid) {
            return this.alert.warning('Revise Los Datos Ingresados');
        } else {
            this.deposito = {
                ...this.deposito,
                descripcion: this.form.get('descripcion').value,
                activo: this.form.get('activo').value,
            }
            if (this.deposito.iddeposito) {
                this.depositosService.updateDeposito(this.deposito)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.onCloseMenu()
                            }
                        }
                    });
            } else {
                this.depositosService.saveDeposito(this.deposito)
                    .subscribe({
                        next: (data) => {
                            if (data) {
                                this.onCloseMenu()
                            }
                        }
                    });
            }
        }
    }

    onCloseMenu() {
        this.dialogRef.close(false)
    }
}